const { Core, Schema, Instance } = require("@hyperjump/json-schema-core");
const Pact = require("@hyperjump/pact");
const { isObject } = require("../common");


const compile = (schema, ast) => Pact.pipeline([
  Schema.entries,
  Pact.map(async ([key, dependency]) => {
    const value = Schema.value(dependency);
    return [key, Array.isArray(value) ? value : await Core.compileSchema(dependency, ast)];
  }),
  Pact.all
], schema);

const interpret = (dependencies, instance, ast) => {
  const value = Instance.value(instance);

  return !isObject(value) || dependencies.every(([propertyName, dependency]) => {
    if (!(propertyName in value)) {
      return true;
    }

    if (Array.isArray(dependency)) {
      return dependency.every((key) => key in value);
    } else {
      return Core.interpretSchema(dependency, instance, ast);
    }
  });
};

module.exports = { compile, interpret };
